<template>
  <div class="context">
    <div class="content">
      <h3 class="title">Создать пользователя:</h3>
      <input id="email-input" class="swal2-input" type="email" placeholder="Введите почту нового пользователя">
      <button v-on:click="createUser" class="btn btn-primary btn-load btn-lg">Создать</button>
    </div>
  </div>
</template>

<script>
import "sweetalert2/dist/sweetalert2.css";
import Connection from "@/rsocket/connection";
import notification from "@/notify/notification";
import Swal from "sweetalert2";

export default {
  name: "CreateUser",
  methods: {
    createUser() {
      let email = ($('#email-input').val() || "").trim();
      if (email.length === 0) {
        notification('warning', 'Введите почту');
        return;
      }
      Swal.fire({
        title: 'Вы уверены?',
        text: "Данное действие создаст нового пользователя!",
        icon: 'question',
        showCancelButton: true,
        confirmButtonText: 'Да! Я знаю, что делаю.',
        cancelButtonText: 'Нет! Я еще подумаю.'
      }).then((result) => {
        if (result.isConfirmed) {
          Connection.createUser(email, (res) => {
            Swal.fire({
              icon: 'success',
              title: 'Готово',
              text: 'Вы успешно добавили нового пользователя! Логин: ' + email.toLowerCase() + " Пароль: " + JSON.parse(res.data.toString()).password
            })
          }, (error) => {
            notification('error', error.prettyText);
          });
        }
      });
    }
  },
  data() {
    return {
      user: [],
    }
  }
}
</script>

<style lang="scss" scoped>
.content {
  box-sizing: border-box;
  background: #222427;
  padding: 10px;
  margin: 10px;
  border-radius: 10px;
  color: white;
  text-align: center;
}
</style>